@import url('https://fonts.googleapis.com/css2?family=Rubik:wght@300;400;500;600&display=swap');

ul {
    list-style: none;
    padding-left: 0;
}

* {
    font-family: 'Rubik', sans-serif;
}


.invalid {
    input{
        border: 0.1rem solid red;
        background-color:#3f1d1da4;
        transition: 300ms;
        &:hover{
        border: 0.1rem solid red;
        background-color:#303245;
          }
    }
    p {
        color:rgb(218, 63, 63);
        margin-top:0;
        transition: 400ms;
    }
}

.site {
    &__description {
        text-align: center;
    }
}

/* Absolute Center Spinner */
.loading {
    position: fixed;
    z-index: 999;
    height: 2em;
    width: 2em;
    overflow: show;
    margin: auto;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
  }
  
  /* Transparent Overlay */
  .loading:before {
    content: '';
    display: block;
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
      background: radial-gradient(rgba(255, 255, 255,.8), rgba(255, 255, 255, .8));
  
    background: -webkit-radial-gradient(rgba(255, 255, 255,.8), rgba(255, 255, 255, .8));
  }
  
  /* :not(:required) hides these rules from IE9 and below */
  .loading:not(:required) {
    /* hide "loading..." text */
    font: 0/0 a;
    color: transparent;
    text-shadow: none;
    background-color: transparent;
    border: 0;
  }
  
  .loading:not(:required):after {
    content: '';
    display: block;
    font-size: 10px;
    width: 1em;
    height: 1em;
    margin-top: -0.5em;
    -webkit-animation: spinner 1250ms infinite linear;
    -moz-animation: spinner 1250ms infinite linear;
    -ms-animation: spinner 1250ms infinite linear;
    -o-animation: spinner 1250ms infinite linear;
    animation: spinner 1250ms infinite linear;
    border-radius: 0.5em;
    -webkit-box-shadow: rgba(104, 104, 104, 0.75) 1.5em 0 0 0, rgba(104, 104, 104, 0.75) 1.1em 1.1em 0 0, rgba(104, 104, 104, 0.75) 0 1.5em 0 0, rgba(104, 104, 104, 0.75) -1.1em 1.1em 0 0, rgba(104, 104, 104, 0.75) -1.5em 0 0 0, rgba(104, 104, 104, 0.75) -1.1em -1.1em 0 0, rgba(104, 104, 104, 0.75) 0 -1.5em 0 0, rgba(104, 104, 104, 0.75) 1.1em -1.1em 0 0;
  box-shadow: rgba(104, 104, 104, 0.75) 1.5em 0 0 0, rgba(104, 104, 104, 0.75) 1.1em 1.1em 0 0, rgba(104, 104, 104, 0.75) 0 1.5em 0 0, rgba(104, 104, 104, 0.75) -1.1em 1.1em 0 0, rgba(104, 104, 104, 0.75) -1.5em 0 0 0, rgba(104, 104, 104, 0.75) -1.1em -1.1em 0 0, rgba(104, 104, 104, 0.75) 0 -1.5em 0 0, rgba(104, 104, 104, 0.75) 1.1em -1.1em 0 0;
  }
  
  /* Animation */
  
  @-webkit-keyframes spinner {
    0% {
      -webkit-transform: rotate(0deg);
      -moz-transform: rotate(0deg);
      -ms-transform: rotate(0deg);
      -o-transform: rotate(0deg);
      transform: rotate(0deg);
    }
    100% {
      -webkit-transform: rotate(360deg);
      -moz-transform: rotate(360deg);
      -ms-transform: rotate(360deg);
      -o-transform: rotate(360deg);
      transform: rotate(360deg);
    }
  }
  @-moz-keyframes spinner {
    0% {
      -webkit-transform: rotate(0deg);
      -moz-transform: rotate(0deg);
      -ms-transform: rotate(0deg);
      -o-transform: rotate(0deg);
      transform: rotate(0deg);
    }
    100% {
      -webkit-transform: rotate(360deg);
      -moz-transform: rotate(360deg);
      -ms-transform: rotate(360deg);
      -o-transform: rotate(360deg);
      transform: rotate(360deg);
    }
  }
  @-o-keyframes spinner {
    0% {
      -webkit-transform: rotate(0deg);
      -moz-transform: rotate(0deg);
      -ms-transform: rotate(0deg);
      -o-transform: rotate(0deg);
      transform: rotate(0deg);
    }
    100% {
      -webkit-transform: rotate(360deg);
      -moz-transform: rotate(360deg);
      -ms-transform: rotate(360deg);
      -o-transform: rotate(360deg);
      transform: rotate(360deg);
    }
  }
  @keyframes spinner {
    0% {
      -webkit-transform: rotate(0deg);
      -moz-transform: rotate(0deg);
      -ms-transform: rotate(0deg);
      -o-transform: rotate(0deg);
      transform: rotate(0deg);
    }
    100% {
      -webkit-transform: rotate(360deg);
      -moz-transform: rotate(360deg);
      -ms-transform: rotate(360deg);
      -o-transform: rotate(360deg);
      transform: rotate(360deg);
    }
  }